@import '../../index.css';

.todohead {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.5rem var(--global-left-padding);
  transition: all 0.1s ease-out 0s;
}

.todohead:hover{
  opacity: 90%;
}

.todohead-row {
  display: flex;
}

.todohead-row__cancel {
  justify-content: space-between;
}

.todohead button {
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  background-color: transparent;
  color: inherit;
  user-select: none;
  border: none;
  transition: all 0.1s ease-out 0s;
}

.todohead button:hover {
  opacity: 50%;
}

.todohead-row__cancel button {
  width: 1.5rem;
  height: 1.5rem;
  border: 2px solid;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;

}

.todohead-row__cancel button span {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Helvetica;
}

/* Note: apparently there is no native way to make textarea expand like a div to accommodate its content
For a hack, try 'The Cleanest Trick for Autogrowing Textareas' on CSS tricks */
.todohead textarea {
  background-color:transparent;
  border: none;
  resize: none;
  font: inherit;
  font-weight:bold;
  outline: none;
  transition: all 0.5s ease-out 0s;
  color: inherit;
  padding: 0px;
}

.todohead textarea::placeholder {
  color: inherit;
}

.todohead .label {
  height: 1.25em;
  margin: 0 0 0.5em 0; 
}

.label textarea {
  width: 33%;
}

.todohead .display {
  margin: 0 0 0.5em 0;
}

.arrowopen {
  transform:rotate(90deg);
}

.todohead h5 {
  flex-grow: 1;
}